@import url('https://fonts.googleapis.com/css?family=Raleway');
@import 'Grid.css';

body {
    background: #222;
    background: -webkit-gradient(linear, left top, right top, from(#da4453), to(#89216b));
    background: -webkit-linear-gradient(left, #da4453, #89216b);
    background: -o-linear-gradient(left, #da4453, #89216b);
    background: linear-gradient(to right, #da4453, #89216b);
    /* background: linear-gradient(to right, #355c7d, #6c5b7b, #c06c84); */
    /* https://uigradients.com/#Quepal */
    color: #fff;
    font-family: 'Raleway', sans-serif;
}

.App {
    margin: 0 auto;
}

.header {
    padding: 1rem;
    background-color: rgba(255,255,255,.2);
    display: -ms-flexbox;
    display: flex;
}

.header .profile {
    -ms-flex-positive: 1;
        flex-grow: 1;
}



