.item-section {
    margin: 1rem;
}

.item-section h1 {
    margin: 0;
}

.items {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.item-container {
    width: 100%;
    height: 133px;
    padding: 0px !important;
}

.item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
}
.item.inactive {
    opacity: .6;
}

.name {
    -ms-flex-positive: 1;
        flex-grow: 1;
    font-size: 24px;
    line-height: 28px;
    font-weight: 300;
    text-align: left;
    padding: 10px;
    background-color: rgba(255, 255, 255, .1);
}

.name .remind-date {
    font-size: .75rem;
}

.actions {
    text-align: right;
    color: #fff;
    -webkit-transition: .3s opacity linear;
    -o-transition: .3s opacity linear;
    transition: .3s opacity linear;
    padding: 5px 0;
}

.channel {
    padding: 2px;
    font-size: 11px;
    display: block;
    border-radius: 2px 2px 0 0;
    text-align: center;
    text-transform: uppercase;
}
.channel.netflix {
    background-color: #b9090b;
}
.channel.rental {
    background-color: #499449;
}
.channel.hulu {
    background-color: #48c083;
}
.channel.showtime {
    background-color: #b20000;
}
.channel.hbo {
    background-color: #222;
}
.channel.comedy-central {
    background-color: #ed4984;
}
.channel.amazon {
    background-color: #01aae1;
}
.channel.fx {
    background-color: #000;
}
.channel.tnt {
    color: #000;
    background-color: rgba(255, 255, 255, 0.5);
}
.channel.amc {
    color: #000;
    background-color: #fff;
}

@media (min-width: 768px) {
    .item-container {
        width: 17%;
    }
    .name {
        font-weight: normal;
    }
}



